.download-box {
  white-space: nowrap;
  background: #fff;
  border: 1px solid #000;
  flex-direction: column;
  justify-content: space-between;
  width: 118px;
  height: 118px;
  padding: 5px;
  font-weight: bold;
  display: flex;
}

.download-box__download-heading {
  flex: auto;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.download-box__download-button-container {
  flex-direction: column;
  display: flex;
}

.download-box__download-button-container_hidden {
  display: none;
}

.download-box__download-button, .download-box__download-as-zip-button, .download-box__download-as-dat-button {
  width: 100%;
  padding: 10px;
  font-size: large;
  font-weight: bold;
}

.download-box__download-links, .download-box__download-zip-link, .download-box__download-dat-links {
  display: none;
}
